export var stringify=function stringify(params){if(!params)return'';var pairs=[];for(var name in params){if(!params.hasOwnProperty(name))continue;if(typeof params[name]==='function')continue;var value=params[name].toString();name=encodeURIComponent(name);value=encodeURIComponent(value);pairs.push(name+'='+value);}return pairs.join('&');};export var parse=function parse(str){return{data:str};};export var Cookies={get:function get(params){var cookie={};var all=document.cookie;if(all===''){return null;}var list=all.split('; ');for(var i=0,len=list.length;i<len;i++){var item=list[i];var p=item.indexOf('=');var name=item.substring(0,p);var value=item.substring(p+1);name=decodeURIComponent(name);value=decodeURIComponent(value);cookie[name]=value;}return cookie[params];},setCookie:function setCookie(name,value,expires,path,domain,secure){var cookie=encodeURIComponent(name)+'='+encodeURIComponent(value);if(expires){cookie+='; expires='+expires.toUTCString();}if(path){cookie+='; path='+path;}if(domain){cookie+='; domain='+domain;}if(secure){cookie+='; secure='+secure;}document.cookie=cookie;},remove:function remove(name){Cookies.setCookie(name,'',new Date(0));}};